.hero {
  background: $hero-background;
}

.hero-title {
  width: 218px;
  margin-bottom: 19px;
}

.title-start {
  display: block;
  font-family: Titan One;
  @include font($fs: 26px, $fw: 400, $lh: 1.154);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $links-text-color;
}

.title-main-word {
  font-family: Titan One;
  @include font($fs: 48px, $fw: 400, $lh: 0.958);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $main-title-color;
}

.hero-btn-pink {
  display: block;
  margin: 0 auto;
  height: 24px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-family: DM Sans;
  @include font(8px, 400, 1.25);
  text-align: center;
  color: $links-text-color;
  background-color: $main-title-color;
  padding: 0 15px;

  &:hover,
  &:focus {
    background-color: #c7133e;
  }
}

.hero-btn-white {
  display: block;
  margin: 0 auto;
  height: 24px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-family: DM Sans;
  @include font(8px, 400, 1.25);
  text-align: center;
  color: $main-title-color;
  background-color: $links-text-color;
  padding: 0 14px;

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.hero-btn-list {
  display: flex;
}

.hero-btn-list-item {
  &:not(:last-child) {
    margin-right: 8px;
  }
}
.hero-container {
  padding-top: 193px;
  padding-bottom: 160px;
  background-image: url('../images/hero/mobile-icecream.png'),
    url('../images/hero/mobile-pink-circle.png');
  background-size: 171px, 284px;
  background-repeat: no-repeat;
  background-position: bottom 0px right 32px, right 0px top 97px;
}

// MOBILE
@media screen and (max-width: 767px) {
  .mb-hide {
    display: none;
  }
}

// TABLET
@media screen and (min-width: 768px) {
  .hero-container {
    padding-top: 98px;
    padding-bottom: 34px;
    background-image: url('../images/hero/tablet-icecream.png'),
      url('../images/hero/tablet-girl.png'),
      url('../images/hero/tablet-pink-circle.png'),
      url('../images/hero/tablet-milk-carafe.png');
    background-size: 143px, 168px, 324px, 124px;
    background-repeat: no-repeat;
    background-position: bottom 0px left 390px, bottom 0px right 30px,
      bottom 47px right 127px, bottom 28px left 35px;
  }

  .presentation {
    display: flex;
    justify-content: space-between;
  }
  .hero-title {
    width: 162px;
    margin-bottom: 20px;
  }

  .title-start {
    @include font($fs: 22px, $fw: 400, $lh: 1.136);
  }

  .title-main-word {
    @include font($fs: 34px, $fw: 400, $lh: 0.912);
  }

  .hero-btn-list {
    display: flex;
    margin-bottom: 66px;
  }

  .hero-paragraph {
    font-family: DM Sans;
    @include font($fs: 8px, $fw: 400, $lh: 1.5);
    color: $links-text-color;
    width: 114px;
    height: 48px;
    margin-left: 120px;
    margin-bottom: 7px;
  }

  .facilities-block {
    position: relative;
    z-index: 2;

    span {
      display: block;
    }

    &:not(:last-child) {
      margin-bottom: 14px;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background-color: $links-text-color;
      width: 16px;
      height: 16px;
      top: -3px;
      right: -8px;
      z-index: -1;
    }
  }

  .facilities-list {
    text-align: right;
  }

  .facilities-amount {
    font-family: Titan One;
    @include font($fs: 28px, $fw: 400, $lh: 1.142);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $main-title-color;
  }

  .facilities-type {
    font-family: DM Sans;
    @include font($fs: 8px, $fw: 400, $lh: 1.25);
    color: $links-text-color;
  }

  .hero-arrow {
    stroke: $main-title-color;
    width: 18px;
    height: 18px;
  }

  .arrow-link {
    margin-left: 120px;
  }
}

// DESKTOP
@media screen and (min-width: 1200px) {
  .hero-container {
    padding-top: 160px;
    padding-bottom: 56px;
    background-image: url('../images/hero/desktop-icecream.png'),
      url('../images/hero/desktop-girl.png'),
      url('../images/hero/desktop-pink-circle.png'),
      url('../images/hero/desktop-milk-carafe.png');
    background-size: 236px, 293px, 538px, 188px;
    background-repeat: no-repeat;
    background-position: bottom -5px left 558px, bottom 0px right 75px,
      top 55px right 225px, bottom 56px left 40px;
  }

  .presentation {
    display: flex;
    justify-content: space-between;
  }

  .hero-title {
    width: 277px;
    margin-bottom: 25px;
  }

  .title-start {
    @include font($fs: 38px, $fw: 400, $lh: 1.157);
  }

  .title-main-word {
    @include font($fs: 58px, $fw: 400, $lh: 0.896);
  }

  .hero-btn-list {
    display: flex;
    margin-bottom: 112px;
  }

  .hero-btn-list-item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  .hero-btn-pink {
    display: block;
    margin: 0 auto;
    height: 40px;
    border-radius: 20px;
    border: none;
    cursor: pointer;
    @include font(14px, 400, 1.286);
    text-align: center;
    color: $links-text-color;
    background-color: $main-title-color;
    padding: 0 24px;
  }

  .hero-btn-white {
    display: block;
    margin: 0 auto;
    height: 40px;
    border-radius: 20px;
    border: none;
    cursor: pointer;
    @include font(14px, 400, 1.286);
    text-align: center;
    color: $main-title-color;
    background-color: $links-text-color;
    padding: 0 21px;
  }

  .hero-paragraph {
    font-family: DM Sans;
    @include font($fs: 14px, $fw: 400, $lh: 1.429);
    color: $links-text-color;
    width: 200px;
    height: 78px;
    margin-left: 180px;
    margin-bottom: 20px;
  }

  .facilities-block {
    position: relative;
    z-index: 2;

    span {
      display: block;
    }

    &:not(:last-child) {
      margin-bottom: 18px;
    }

    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background-color: $links-text-color;
      width: 30px;
      height: 30px;
      top: -6px;
      right: -15px;
      z-index: -1;
    }
  }

  .facilities-list {
    text-align: right;
  }

  .facilities-amount {
    font-family: Titan One;
    @include font($fs: 46px, $fw: 400, $lh: 1.152);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $main-title-color;
  }

  .facilities-type {
    font-family: DM Sans;
    @include font($fs: 14px, $fw: 400, $lh: 1.286);
    color: $links-text-color;
  }

  .hero-arrow {
    stroke: $main-title-color;
    width: 26px;
    height: 26px;
  }

  .arrow-link {
    margin-left: 180px;
  }
}
